/*variables*/
$font-stack: "Inter", "Jost", sans-serif;
$font-main: "Inter", sans-serif;
$font-secondary: "Jost", sans-serif;
$bg-main-color: #f5f6f8;
$bg-secondary: #26e6e6;
$color-grey: #919193;
$color-white: #fff;
$color-black: #232323;
$color-wrong: rgba(219, 16, 16, 0.856);

/*Media quieries mixins*/
@mixin xs-devices {
  @media (max-width: 575px) {
    @content;
  }
}
@mixin sm-devices {
  @media (max-width: 767px) {
    @content;
  }
}
@mixin md-devices {
  @media(min-width: 768px) and (max-width: 991px) {
    @content;
  }
}
@mixin lg-devices {
  @media (min-width: 992px) and (max-width: 1199px) {
    @content;
  }
}
@mixin xl-devices {
  @media (min-width: 1200px) {
    @content;
  }
}
@mixin landscape-orientation {
  @media (max-width: 991px) and (orientation: landscape) {
    @content;
  }
}

/*keyframes*/
  @keyframes slideShow {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes slideShow  {
    0% {
    }
    5%,25% {
       opacity: 0.4;
    }
    15%,35% {
      opacity: 0.7;
    }
    40% {
      opacity: 1;
  }
}


